//  vue.js 
import Vue from 'vue'
//  App.vue 
import App from './App.vue'
//  index.js import router from './router/index.js'
import router from './router'
// 
import './directives'
// 
import './components'
//  store/index.js 
import store from './store'
// 
import VueSweetalert from './plugins/vue-sweetalert'
import Message from './plugins/message'
import './filters'
import { mockArticles } from './mock/data'
import ls from './utils/localStorage'
//  ./mock/index.js
import './mock'
//  axios 
import axios from 'axios'

//  axios  .Vue.prototype 
Vue.prototype.$axios = axios

// 
Vue.use(VueSweetalert)
Vue.use(Message)

// eslint-disable-next-line no-unused-vars
const AddMockData = (() => {
    // 
    const isAddMockData = true
    // 
    let userArticles = ls.getItem('articles')

    if (Array.isArray(userArticles)) {
        userArticles = userArticles.filter(article => parseInt(article.uid) === 1)
    } else {
        userArticles = []
    }

    if (isAddMockData) {
        // 
        store.commit('UPDATE_ARTICLES', [...userArticles, ...mockArticles(60)])
    } else {
        // 
        store.commit('UPDATE_ARTICLES', userArticles)
    }
})()

//  false  Vue 
Vue.config.productionTip = false

//  Vue 
new Vue({
    render: h => h(App),
    router,
    store,
}).$mount('#app')
