import Vue from 'vue'
import validator from './validator'
import dropdown from './dropdown'
//  title.js 
import title from './title'

const directives = {
    validator,
    dropdown,
    //  title 
    title
}

/**
 * Object.entries  Object.entries(directives) 
 * [['validator', {...}], ['dropdown', {...}]]
 */
for (const [key, value] of Object.entries(directives)) {
    Vue.directive(key, value)
}